@import "./logo";

$header-height: 36px;

* {
    padding: 0;
    margin: 0;
}

.viewport {
    flex-grow: 1;
    max-height: 100%;
    font-family: monospace;

    &-main {
        display: flex;
        flex-direction: column;
        padding: 60px 0;
    }

    &-content {
        flex-grow: 1;
        position: relative;
        display: flex;
        flex-direction: column;
        overflow-y: auto;
        overflow-x: hidden;

        .viewport-header {
            position: fixed;
            top: 0;
            left: 0;
            right: 0;
            display: flex;
            flex-wrap: wrap;
            align-items: center;
            justify-content: space-between;
            height: $header-height;
            z-index: 800;
            margin: 25px;
        }

        .viewport-footer {
            position: fixed;
            bottom: 0;
            right: 0;
            left: 0;
            background-color: white;
        }

        .viewport-body {
            position: relative;
            overflow-y: auto;
            overflow-x: hidden;
            height: 100vh;
            width: 100%;
        }
    }
}

.btn {
    display: flex;
    align-items: center;
    background-color: transparent;
    border-color: transparent;

    &-icon {
        width: 1.5rem;
        height: 1.5rem;
    }
}
